<template>
  <div class="container">
    <h1 class="title">F & A</h1>
    <ul class="qna_container">
      <li
        class="qna_item"
        :class="state[i]"
        v-for="(item, i) in items"
        :key="item.id"
        @click="open(i)"
        ref="item"
      >
        <p>{{ item.question }}</p>
        <button class="drop_icon"></button>
        <ul class="answer">
          <li>
            {{ item.answer }}
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      items: [
        {
          id: 1,
          question: '  ?',
          answer:
            '  login      .'
        },
        {
          id: 2,
          question: '     ?',
          answer: '      '
        },
        {
          id: 3,
          question: '    ?',
          answer:
            '        ,              .'
        },
        {
          id: 4,
          question: '      ?',
          answer: `1)  , , ,          ,     .\n
2)            .\n
3)         , , ,  ,     .\n
4)           .\n
5)                    .\n
6)            .\n
7)   ,  , ,     .\n
8)         .\n
9)        ,         .\n
`
        }
      ],
      state: []
    }
  },
  methods: {
    open(id) {
      if (this.state[id] === 'off') this.state[id] = 'on'
      else {
        this.state[id] = 'off'
      }

      console.log(this.state)
    }
  },
  mounted() {
    // qna        off  
    this.state = new Array(this.items.length).fill('off')
  }
}
</script>
<style scoped>
li {
  list-style: none;
}
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  min-height: 100vh;
}

.title {
  text-align: center;
  margin: 50px;
}

.qna_container {
  padding: 10px;
}

.qna_item {
  position: relative;
}

.qna_item p {
  color: rgb(0, 0, 0);
  border-radius: 5px;
  padding: 20px;
  box-shadow: inset 0 -2px 3px rgb(190, 163, 11);
  background-color: rgb(224, 165, 37);
}

.qna_item:nth-of-type(2n) p {
  background-color: rgb(255, 224, 123);
}

.answer {
  transition: 0.1s;
  z-index: -1;
  visibility: hidden;

  height: 0;
  opacity: 0;
  transform: translate(0, -5px);
}

.drop_icon {
  border: none;
  background-color: transparent;
  color: rgb(50, 49, 49);
  position: absolute;
  transition: 0.5s;
  right: 5px;
  font-size: 1.25rem;
  padding: 10px;
  top: 12px;
}

.qna_item.off .drop_icon::before {
  content: '';
  transition: 0.5s;
}

.qna_item.on .drop_icon::before {
  content: '';
  transition: 0.5s;
}
.qna_item.on .answer {
  padding: 20px 5px;
  border: 1px solid dashed tomato;
  width: 100%;
  height: 100%;
  display: block;
  visibility: visible;
  opacity: 1;
  transform: translate(0, 0);
}
</style>
