<template>
  <article class="like_box">
    <p @click="likeCounter">
      <span>{{ liked }}</span>
    </p>
    <p @click="unlikeCounter">
      <span>{{ unliked }}</span>
    </p>
  </article>
</template>
<script setup>
import axios from 'axios'
import { ref, reactive } from 'vue'
import { useRoute } from 'vue-router'

//    .
const liked = ref(0)
const unliked = ref(0)

//    .
const route = ref(useRoute().params.id)

//   
const userInfo = reactive({
  postId: route,
  username: document.cookie.split('=')[1],
  like: false,
  unlike: false
})

/*    */
function likeCounter() {
  //     /  
  userInfo.like = !userInfo.like
  //  true   1  .
  if (userInfo.like) {
    liked.value++
    //  true , 1 1 .
    if (userInfo.unlike && unliked.value > 0) {
      unliked.value--
      userInfo.unlike = false
    }
    sendUserInfoToServer(userInfo)
  } else {
    liked.value--
  }

  console.log(userInfo)
}
function unlikeCounter() {
  userInfo.unlike = !userInfo.unlike

  //     1  .
  if (userInfo.unlike) {
    unliked.value++
    //    ,  1 
    //  1  ,   .
    if (userInfo.like && liked.value > 0) {
      liked.value--
      userInfo.like = false
    }
    sendUserInfoToServer(userInfo)
    //    1  .
  } else {
    unliked.value--
  }
  console.log(userInfo)
}

/*     . */
const sendUserInfoToServer = async (updateUserInfo) => {
  await axios
    .post(
      `http://localhost:3000/board/${route.value}/like-user`,
      updateUserInfo
    )
    .then((response) => {
      console.log(' :', response.statusText)
    })
    .catch((error) => {
      console.log(error)
    })
}

/*   /    . */
/* const getPostLikeInfoFromServer = () => {
  axios
    .get(`/board/${route.value}/like-counter`)
    .then((response) => {
      console.log('    :', response)
    })
    .catch((error) => {
      console.log(error)
    })
}
getPostLikeInfoFromServer()
*/
</script>
<style scoped>
/* / */
.like_box {
  display: flex;
  padding: 15px;
  justify-content: center;
}

.like_box p {
  margin: 0 5px;
  box-shadow: 1px 1px 3px rgb(176, 131, 17);
  padding: 6px 10px;
  border-radius: 10px;
}
</style>
