<template>
  <div class="page_container">
    <div class="hospital_title">
      <h1>  </h1>
      <span class="page_message"
        > : {{ currentPage }}/{{ totalPage }}</span
      >
    </div>
    <div class="region_box">
      <button
        class="region_menu"
        v-for="p in region"
        :key="p"
        @click="getArea($event)"
        :class="{ selected: p === area }"
      >
        {{ p }}
      </button>
      <button
        class="near_btn"
        @click="locMarker"
        :class="{ selected: locArea === ' ' }"
      >
         
      </button>
    </div>
    <div class="hospital_content">
      <div class="hospital_list">
        <ul>
          <li
            class="hospital_name"
            v-for="data in hospitalInfo"
            :key="data.id"
            @click="loadScript(data.address, data.name)"
          >
            {{ data.name }}
          </li>
        </ul>
      </div>
      <div class="map_box">
        <div class="map" ref="map" style="width: 500px; height: 400px"></div>
      </div>
    </div>
    <nav class="pagination_nav">
      <ul class="pagination_box">
        <li class="page_shifter" @click="prevPage" v-show="currentPage !== 1">
          prev
        </li>
        <li
          class="page_shifter"
          v-for="(page, i) in pageList"
          :key="i"
          @click="getNextPage"
        >
          {{ page }}
        </li>
        <li
          class="page_shifter"
          @click="nextPage"
          v-show="totalPage > lastPage"
        >
          next
        </li>
      </ul>
    </nav>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'hospital',
  data() {
    return {
      hospitalInfo: {},
      region: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      area: '',
      locArea: '',
      currentPage: 1,
      firstPage: 1,
      displayPage: 5,
      lastPage: 5,
      currentPageGroup: Math.ceil(this.currentPage / this.displayPage),
      totalInfoCnt: 0,
      pageList: [1, 2, 3, 4, 5],
      maxPage: 0,
      totalPage: 0,
      focusPage: 1,
      focus: '',
      latlng: [],
      positions: []
    }
  },
  methods: {
    async loadScript(a, n) {
      if (!window.kakao) {
        const script = document.createElement('script')
        script.src = `https://dapi.kakao.com/v2/maps/sdk.js?appkey=${process.env.VUE_APP_KAKAO_API_KEY}&libraries=services,drawing&autoload=false`
        script.async = true

        script.onload = () => {
          window.kakao.maps.load(() => {
            this.loadMap(a, n)
          })
        }
        document.head.appendChild(script)
      } else {
        this.loadMap(a, n)
      }
    },
    loadMap(a, n) {
      const mapContainer = this.$refs.map
      const mapOptions = {
        center: new window.kakao.maps.LatLng(35.152381, 129.059767),
        level: 4
      }
      const map = new window.kakao.maps.Map(mapContainer, mapOptions)

      const geocoder = new window.kakao.maps.services.Geocoder()

      //   
      geocoder.addressSearch(a, function (result, status) {
        //   
        if (status === window.kakao.maps.services.Status.OK) {
          const coords = new window.kakao.maps.LatLng(result[0].y, result[0].x)

          //     
          const marker = new window.kakao.maps.Marker({
            map: map,
            position: coords
          })

          //     
          const infowindow = new window.kakao.maps.InfoWindow({
            content: `<div style="width:150px;text-align:center;padding:6px 0;">${n}</div>`
          })
          infowindow.open(map, marker)

          //      
          map.setCenter(coords)
        }
      })
    },
    locMarker() {
      this.locArea = ' '
      this.area = ''
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            const latitude = position.coords.latitude
            const longitude = position.coords.longitude

            const mapContainer = document.querySelector('.map')
            const mapOptions = {
              center: new window.kakao.maps.LatLng(latitude, longitude),
              level: 5
            }
            const map = new window.kakao.maps.Map(mapContainer, mapOptions)

            //    
            const markerPosition = new window.kakao.maps.LatLng(
              latitude,
              longitude
            )

            const marker = new window.kakao.maps.Marker({
              map: map,
              position: markerPosition
            })

            const infowindow = new window.kakao.maps.InfoWindow({
              content: `<div style="width:150px;text-align:center;padding:3px 0;"> </div>`
            })
            infowindow.open(map, marker)
            this.getNearbyHospital(map.getCenter())
            map.setCenter(markerPosition)
          },
          (error) => {
            console.error('   .:', error)
          }
        )
      } else {
        console.error('   .')
      }
    },
    async getNearbyHospital(center) {
      const geocoder = new window.kakao.maps.services.Geocoder()
      await geocoder.coord2RegionCode(
        center.getLng(),
        center.getLat(),
        async (result, status) => {
          if (status === window.kakao.maps.services.Status.OK) {
            const getAddress = result[0].address_name.split(' ')
            const index = getAddress.length - 2
            const address = getAddress[index]
            this.area = address
            console.log(this.area)

            this.getAreaInfo()
          }
        }
      )
    },
    async getArea(e) {
      this.area = e.target.innerText
      this.currentPage = 1
      this.locArea = ''
      await this.getAreaInfo()
    },
    //       
    getNextPage(e) {
      console.log(e.target.innerText)
      this.currentPage = e.target.innerText * 1
      this.getAreaInfo()
      this.paginationControlFunc(this.currentPage)
    },
    //    
    paginationControlFunc(currentPage) {
      window.scrollTo({ top: 0, behavior: 'smooth' })
      this.currentPageGroup = Math.ceil(currentPage / this.displayPage)
      this.lastPage = this.displayPage * this.currentPageGroup
      this.firstPage = this.lastPage - this.displayPage + 1

      console.log(':', this.firstPage)
      console.log(':', this.lastPage)
      console.log(':', this.currentPage)
      console.log(this.pageList.length)
      const list = []
      if (this.lastPage >= this.totalPage) {
        this.lastPage = this.totalPage
      }
      for (let i = this.firstPage; i <= this.lastPage; i++) {
        list.push(i)
      }
      this.pageList = list
      console.log(this.pageList)
    },
    //    
    nextPage() {
      console.log('  ')
      console.log(this.maxPage)
      if (this.currentPage <= this.maxPage) {
        this.currentPage++
        this.getAreaInfo()
        this.paginationControlFunc(this.currentPage)
        console.log(':', this.currentPage)
      }
    },
    //    
    prevPage() {
      console.log('  ')
      if (this.currentPage > 1) {
        this.currentPage = this.currentPage - 1
        this.getAreaInfo()
        this.paginationControlFunc(this.currentPage)
      }
      console.log(':', this.currentPage)
    },
    //     
    getAreaInfo() {
      axios
        .get(
          `http://localhost:3000/tools/hospital/?region=${this.area}&page=${
            this.currentPage || 1
          }`
        )
        .then((response) => {
          if (response.status === 200) {
            this.hospitalInfo = response.data.result
            this.totalInfoCnt = response.data.totalCount * 1
            this.maxPage = this.totalInfoCnt / this.displayPage
            this.totalPage = response.data.totalPage
            this.focusPage = response.data.page
            console.log(this.hospitalInfo)
          }
        })
        .catch((error) => {
          console.log(
            '    .:',
            error
          )
        })
    }
  },
  //    
  async mounted() {
    await axios
      .get('http://localhost:3000/tools/hospital')
      .then((response) => {
        if (response.status === 200) {
          this.hospitalInfo = response.data.result
          this.totalPage = response.data.totalPage
        }
      })
      .catch((error) => {
        console.log(
          '    .:',
          error
        )
      })
    if (window.kakao && window.kakao.maps) {
      //   ,       
      this.locMarker()
    } else {
      //       
      this.loadScript()
    }
  }
}
</script>

<style scoped>
.page_container {
  min-height: 100vh;
  animation: appear 1s 1 ease-in-out;
}

@keyframes appear {
  from {
    opacity: 0;
    visibility: hidden;
  }
}
.hospital_title {
  text-align: center;
  margin: 30px;
  border-bottom: 1px solid gray;
}
.region_box {
  padding: 10px;
  border-bottom: 1px solid gray;
  text-align: center;
}
.region_menu {
  border: none;
  border-radius: 10px;
  margin: 0 0 10px 10px;
  padding: 5px;
  background: beige;
  transition: 0.1s ease-in;
}
.region_menu:hover {
  transform: scale(1.01);
  background: rgb(228, 185, 169);
  box-shadow: 3px 3px 5px gray;
  cursor: pointer;
}
.region_menu:active {
  transform: scale(1);
  background: rgb(219, 141, 113);
  box-shadow: -1px -1px 3px gray;
}
.region_menu.selected {
  background: rgb(228, 185, 169);
  box-shadow: -2px -2px 5px gray;
}
.page_message {
  position: fixed;
  font-size: 14px;
  bottom: 300px;
  right: 5px;
  background: rgb(253, 241, 172);
  box-shadow: inset 1px 1px 5px 2px rgb(245, 219, 69);
  padding: 10px;
  border-radius: 10px;
}
/*  */
.pagination_nav {
  width: 70%;
  margin: 10px auto;
}
.pagination_box {
  display: flex;
  justify-content: center;
  align-content: center;
}
.page_shifter {
  border: 1px solid rgba(128, 128, 128, 0.807);
  list-style: none;
  text-align: center;
  width: 25px;
  height: 25px;
  border-radius: 5px;
  margin: 3px;
  padding: 11px 10px 5px 10px;
}
.page_shifter:hover {
  background: rgb(245, 142, 8);
  cursor: pointer;
}
.hospital_content {
  display: flex;
}
.hospital_content {
  display: flex;
}
.hospital_list {
  margin: 20px;
  width: 50%;
  background: #f0f0d4;
  color: rgb(175, 120, 17);
  padding: 10px 0;
}
.hospital_name {
  font-weight: 800;
  font-size: 1.2em;
  width: 100%;
  list-style: none;
  transition: 0.1s;
  margin-bottom: 5px;
  border-bottom: 1px solid gray;
}
.hospital_name:hover {
  transform: translateX(10px);
  color: rgb(175, 172, 17);
  background: #ececde;
  box-shadow: 1px 1px 5px gray;
  cursor: pointer;
}
.hospital_name:active {
  box-shadow: -1px -1px 5px gray;
}
.near_btn {
  border: none;
  font-weight: 800;
  font-size: 1.3em;
  margin-bottom: 5px;
  color: rgb(233, 124, 142);
  list-style: none;
  transition: 0.1s;
  margin: 0 10px;
}
.near_btn:hover {
  transform: translateX(10px);
  color: rgb(247, 43, 77);
  background: #ececde;
  box-shadow: 1px 1px 5px gray;
}
.near_btn:active {
  box-shadow: -1px -1px 5px gray;
}
.near_btn.selected {
  background: rgb(231, 230, 195);
  box-shadow: -2px -2px 5px gray;
}
.map_box {
  margin: 20px;
}
.map {
  margin: auto;
}
</style>
